"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { DashboardLayout } from "@/components/dashboard-layout"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Switch } from "@/components/ui/switch"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Bell, Globe, Moon, Shield, Trash2, Download, LogOut, Crown } from "lucide-react"
import { useAuth } from "@/lib/auth-context"
import { useTheme } from "@/lib/theme-context"
import { useI18n } from "@/lib/i18n-context"
import { toast } from "sonner"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"

export default function SettingsPage() {
  const { user, logout, updateUser } = useAuth()
  const { theme, setTheme } = useTheme()
  const { language, setLanguage, t } = useI18n()
  const router = useRouter()

  const [emailNotifications, setEmailNotifications] = useState(true)
  const [marketingEmails, setMarketingEmails] = useState(false)
  const [selectedPlan, setSelectedPlan] = useState<string | null>(null)

  const handleExportData = () => {
    toast.success(t.settings.privacy.exportSuccess)
  }

  const handleLogout = async () => {
    await logout()
    router.push("/")
    toast.success("Logged out successfully")
  }

  const handlePlanChange = async (newPlan: string) => {
    setSelectedPlan(newPlan)
  }

  const confirmPlanChange = async () => {
    if (!selectedPlan) return
    try {
      await updateUser({ plan: selectedPlan as "free" | "pro" | "enterprise" })
      toast.success(`Plan updated to ${selectedPlan}`)
      setSelectedPlan(null)
    } catch (error) {
      toast.error("Failed to update plan")
      setSelectedPlan(null)
    }
  }

  const handleDeleteAccount = () => {
    logout()
    router.push("/")
    toast.success(t.settings.danger.deleteSuccess)
  }

  if (!user) return null

  return (
    <DashboardLayout>
      <div className="max-w-3xl mx-auto">
        <h1 className="text-2xl md:text-3xl font-bold">{t.settings.title}</h1>
        <p className="mt-1 text-muted-foreground">{t.settings.subtitle}</p>

        <div className="mt-8 space-y-6">
          {/* Subscription Plan */}
          <Card className="border-border/40 bg-card/30">
            <CardHeader>
              <CardTitle className="text-lg flex items-center gap-2">
                <Crown className="h-5 w-5 text-chart-2" />
                Subscription Plan
              </CardTitle>
              <CardDescription>Manage your current plan and upgrade if needed</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="mb-4">
                <p className="text-sm font-medium">Current Plan: <span className="text-primary capitalize font-semibold">{user.plan}</span></p>
                <p className="text-sm text-muted-foreground">Credits remaining: {user.credits}</p>
              </div>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                {["free", "pro", "enterprise"].map((plan) => (
                  <div
                    key={plan}
                    className={`rounded-lg border-2 p-4 transition-all ${
                      user.plan === plan
                        ? "border-primary bg-primary/5"
                        : "border-border/40 hover:border-border/60"
                    }`}
                  >
                    <p className="font-semibold capitalize mb-2">{plan}</p>
                    <p className="text-sm text-muted-foreground mb-4">
                      {plan === "free" && "Get started free"}
                      {plan === "pro" && "For creators"}
                      {plan === "enterprise" && "Advanced features"}
                    </p>
                    {user.plan !== plan && (
                      <AlertDialog>
                        <AlertDialogTrigger asChild>
                          <Button variant="outline" size="sm" className="w-full gap-2">
                            Change Plan
                          </Button>
                        </AlertDialogTrigger>
                        <AlertDialogContent>
                          <AlertDialogHeader>
                            <AlertDialogTitle>Change Subscription Plan</AlertDialogTitle>
                            <AlertDialogDescription>
                              Are you sure you want to change to the <span className="font-semibold capitalize text-foreground">{plan}</span> plan? Your current plan will be replaced.
                            </AlertDialogDescription>
                          </AlertDialogHeader>
                          <AlertDialogFooter>
                            <AlertDialogCancel>Cancel</AlertDialogCancel>
                            <AlertDialogAction
                              onClick={() => {
                                setSelectedPlan(plan)
                                confirmPlanChange()
                              }}
                              className="bg-primary hover:bg-primary/90"
                            >
                              Confirm Change
                            </AlertDialogAction>
                          </AlertDialogFooter>
                        </AlertDialogContent>
                      </AlertDialog>
                    )}
                    {user.plan === plan && (
                      <Button variant="outline" size="sm" className="w-full" disabled>
                        Current Plan
                      </Button>
                    )}
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Appearance */}
          <Card className="border-border/40 bg-card/30">
            <CardHeader>
              <CardTitle className="text-lg flex items-center gap-2">
                <Moon className="h-5 w-5" />
                Appearance
              </CardTitle>
              <CardDescription>Customize how VidInsight looks</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center justify-between">
                <div>
                  <Label>Theme</Label>
                  <p className="text-sm text-muted-foreground">Select your preferred theme</p>
                </div>
                <Select value={theme} onValueChange={(value: "dark" | "light") => setTheme(value)}>
                  <SelectTrigger className="w-32">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="dark">Dark</SelectItem>
                    <SelectItem value="light">Light</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </CardContent>
          </Card>

          {/* Language */}
          <Card className="border-border/40 bg-card/30">
            <CardHeader>
              <CardTitle className="text-lg flex items-center gap-2">
                <Globe className="h-5 w-5" />
                {t.settings.language.title}
              </CardTitle>
              <CardDescription>{t.settings.language.description}</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="flex items-center justify-between">
                <div>
                  <Label>{t.settings.language.language}</Label>
                  <p className="text-sm text-muted-foreground">{t.settings.language.languageDescription}</p>
                </div>
                <Select value={language} onValueChange={(value: "fr" | "en" | "es" | "de") => setLanguage(value)}>
                  <SelectTrigger className="w-40">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="fr">{t.settings.language.french}</SelectItem>
                    <SelectItem value="en">{t.settings.language.english}</SelectItem>
                    <SelectItem value="es">{t.settings.language.spanish}</SelectItem>
                    <SelectItem value="de">{t.settings.language.german}</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </CardContent>
          </Card>

          {/* Notifications */}
          <Card className="border-border/40 bg-card/30">
            <CardHeader>
              <CardTitle className="text-lg flex items-center gap-2">
                <Bell className="h-5 w-5" />
                {t.settings.notifications.title}
              </CardTitle>
              <CardDescription>{t.settings.notifications.description}</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center justify-between">
                <div>
                  <Label>{t.settings.notifications.emailNotifications}</Label>
                  <p className="text-sm text-muted-foreground">{t.settings.notifications.emailDescription}</p>
                </div>
                <Switch checked={emailNotifications} onCheckedChange={setEmailNotifications} />
              </div>
              <div className="flex items-center justify-between">
                <div>
                  <Label>{t.settings.notifications.marketingEmails}</Label>
                  <p className="text-sm text-muted-foreground">{t.settings.notifications.marketingDescription}</p>
                </div>
                <Switch checked={marketingEmails} onCheckedChange={setMarketingEmails} />
              </div>
            </CardContent>
          </Card>

          {/* Privacy & Security */}
          <Card className="border-border/40 bg-card/30">
            <CardHeader>
              <CardTitle className="text-lg flex items-center gap-2">
                <Shield className="h-5 w-5" />
                {t.settings.privacy.title}
              </CardTitle>
              <CardDescription>{t.settings.privacy.description}</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center justify-between">
                <div>
                  <Label>{t.settings.privacy.exportData}</Label>
                  <p className="text-sm text-muted-foreground">{t.settings.privacy.exportDescription}</p>
                </div>
                <Button variant="outline" size="sm" onClick={handleExportData}>
                  <Download className="mr-2 h-4 w-4" />
                  {t.settings.privacy.export}
                </Button>
              </div>
            </CardContent>
          </Card>

          {/* Danger Zone */}
          <Card className="border-destructive/40 bg-destructive/5">
            <CardHeader>
              <CardTitle className="text-lg flex items-center gap-2 text-destructive">
                <Trash2 className="h-5 w-5" />
                {t.settings.danger.title}
              </CardTitle>
              <CardDescription>{t.settings.danger.description}</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center justify-between pb-4 border-b border-destructive/20">
                <div>
                  <Label className="text-base">Sign Out</Label>
                  <p className="text-sm text-muted-foreground">Log out from your account</p>
                </div>
                <AlertDialog>
                  <AlertDialogTrigger asChild>
                    <Button variant="outline" size="sm" className="gap-2">
                      <LogOut className="h-4 w-4" />
                      Sign Out
                    </Button>
                  </AlertDialogTrigger>
                  <AlertDialogContent>
                    <AlertDialogHeader>
                      <AlertDialogTitle>Sign Out</AlertDialogTitle>
                      <AlertDialogDescription>
                        Are you sure you want to sign out from your account?
                      </AlertDialogDescription>
                    </AlertDialogHeader>
                    <AlertDialogFooter>
                      <AlertDialogCancel>Cancel</AlertDialogCancel>
                      <AlertDialogAction
                        onClick={handleLogout}
                        className="bg-primary hover:bg-primary/90"
                      >
                        Sign Out
                      </AlertDialogAction>
                    </AlertDialogFooter>
                  </AlertDialogContent>
                </AlertDialog>
              </div>
              <div className="flex items-center justify-between">
                <div>
                  <Label>{t.settings.danger.deleteAccount}</Label>
                  <p className="text-sm text-muted-foreground">{t.settings.danger.deleteDescription}</p>
                </div>
                <AlertDialog>
                  <AlertDialogTrigger asChild>
                    <Button variant="destructive" size="sm">
                      {t.settings.danger.deleteAccount}
                    </Button>
                  </AlertDialogTrigger>
                  <AlertDialogContent>
                    <AlertDialogHeader>
                      <AlertDialogTitle>{t.settings.danger.deleteConfirmTitle}</AlertDialogTitle>
                      <AlertDialogDescription>
                        {t.settings.danger.deleteConfirmDescription}
                      </AlertDialogDescription>
                    </AlertDialogHeader>
                    <AlertDialogFooter>
                      <AlertDialogCancel>{t.settings.danger.cancel}</AlertDialogCancel>
                      <AlertDialogAction
                        onClick={handleDeleteAccount}
                        className="bg-destructive hover:bg-destructive/90"
                      >
                        {t.settings.danger.delete}
                      </AlertDialogAction>
                    </AlertDialogFooter>
                  </AlertDialogContent>
                </AlertDialog>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </DashboardLayout>
  )
}
